import { Graph } from "../Elements/Graph";
import { GraphParser } from "./GraphParser";
export var GraphRepresentationType;
(function (GraphRepresentationType) {
    GraphRepresentationType[GraphRepresentationType["ADJACENCY_LIST"] = 0] = "ADJACENCY_LIST";
    GraphRepresentationType[GraphRepresentationType["ADJACENCY_MATRIX"] = 1] = "ADJACENCY_MATRIX";
    GraphRepresentationType[GraphRepresentationType["EDGE_LIST"] = 2] = "EDGE_LIST";
})(GraphRepresentationType || (GraphRepresentationType = {}));
;
export class GraphRepresentationConverter {
    constructor(onError) {
        this.from = "";
        this.to = "";
        this.fromType = GraphRepresentationType.ADJACENCY_LIST;
        this.toType = GraphRepresentationType.ADJACENCY_LIST;
        this.isWeighted = false;
        this.vertices = "-";
        this.inDegrees = "-";
        this.outDegrees = "-";
        this.directedEulerianPath = "-";
        this.undirectedEulerianPath = "-";
        this.isBipartite = null;
        this.isCyclic = null;
        this.isConnected = null;
        this.isUndirected = null;
        this.onError = onError;
    }
    convert() {
        try {
            this.to = this.convertTo(this.convertFrom());
        }
        catch (e) {
            this.onError("Failed to parse the inputted graph");
            return;
        }
    }
    convertFrom() {
        let parsed;
        if (this.isWeighted) {
            switch (this.fromType) {
                case GraphRepresentationType.ADJACENCY_LIST:
                    parsed = GraphParser.parseWeightedAdjacencyListString(this.from);
                    break;
                case GraphRepresentationType.ADJACENCY_MATRIX:
                    parsed = GraphRepresentationConverter.fromWeightedAdjacencyMatrix(GraphParser.parseAdjacencyMatrixString(this.from));
                    break;
                case GraphRepresentationType.EDGE_LIST:
                    parsed = GraphRepresentationConverter.fromWeightedEdgeList(GraphParser.parseWeightedEdgeListString(this.from));
                    break;
            }
        }
        else {
            switch (this.fromType) {
                case GraphRepresentationType.ADJACENCY_LIST:
                    parsed = GraphParser.parseAdjacencyListString(this.from);
                    break;
                case GraphRepresentationType.ADJACENCY_MATRIX:
                    parsed = GraphRepresentationConverter.fromAdjacencyMatrix(GraphParser.parseAdjacencyMatrixString(this.from));
                    break;
                case GraphRepresentationType.EDGE_LIST:
                    parsed = GraphRepresentationConverter.fromEdgeList(GraphParser.parseEdgeListString(this.from));
                    break;
            }
        }
        return parsed;
    }
    convertTo(representation) {
        let stringified;
        let converted;
        if (this.isWeighted) {
            switch (this.toType) {
                case GraphRepresentationType.ADJACENCY_LIST:
                    stringified = GraphParser.stringifyWeightedAdjacencyList(representation);
                    break;
                case GraphRepresentationType.ADJACENCY_MATRIX:
                    converted = GraphRepresentationConverter.toWeightedAdjacencyMatrix(representation);
                    stringified = GraphParser.stringifyAdjacencyMatrix(converted);
                    break;
                case GraphRepresentationType.EDGE_LIST:
                    converted = GraphRepresentationConverter.toWeightedEdgeList(representation);
                    stringified = GraphParser.stringifyEdgeList(converted);
                    break;
            }
        }
        else {
            switch (this.toType) {
                case GraphRepresentationType.ADJACENCY_LIST:
                    stringified = GraphParser.stringifyAdjacencyList(representation);
                    break;
                case GraphRepresentationType.ADJACENCY_MATRIX:
                    converted = GraphRepresentationConverter.toAdjacencyMatrix(representation);
                    stringified = GraphParser.stringifyAdjacencyMatrix(converted);
                    break;
                case GraphRepresentationType.EDGE_LIST:
                    converted = GraphRepresentationConverter.toEdgeList(representation);
                    stringified = GraphParser.stringifyEdgeList(converted);
                    break;
            }
        }
        return stringified;
    }
    analyze() {
        try {
            const adjlist = this.convertFrom();
            let graph;
            if (this.isWeighted)
                graph = new Graph(adjlist);
            else
                graph = new Graph(Graph.toWeightedAdjacencyList(adjlist));
            this.vertices = graph.vertices.join(", ");
            this.inDegrees = Object.entries(graph.inDegrees).map(x => `(${x[0]},${x[1]})`).join(", ");
            this.outDegrees = Object.entries(graph.outDegrees).map(x => `(${x[0]},${x[1]})`).join(", ");
            const directedEulerTour = graph.eulerTour();
            const undirectedEulerTour = graph.undirectedEulerTour();
            this.directedEulerianPath = directedEulerTour.length === 0 ? "None" : directedEulerTour.join(" => ");
            this.undirectedEulerianPath = undirectedEulerTour.length === 0 ? "None" : undirectedEulerTour.join(" => ");
            this.isUndirected = graph.isUndirected();
            this.isBipartite = graph.isBipartite();
            this.isConnected = graph.isConnected();
            this.isCyclic = graph.isCyclic();
        }
        catch (e) {
            this.onError("Failed to parse the inputted graph");
            return;
        }
    }
    addRemoveWeight() {
        try {
            if (this.isWeighted) {
                let parsed;
                let light;
                switch (this.fromType) {
                    case GraphRepresentationType.ADJACENCY_LIST:
                        parsed = GraphParser.parseWeightedAdjacencyListString(this.from);
                        light = GraphRepresentationConverter.removeWeightFromAdjacencyList(parsed);
                        this.from = GraphParser.stringifyAdjacencyList(light);
                        break;
                    case GraphRepresentationType.ADJACENCY_MATRIX:
                        parsed = GraphParser.parseAdjacencyMatrixString(this.from);
                        light = GraphRepresentationConverter.removeWeightFromAdjacencyMatrix(parsed);
                        this.from = GraphParser.stringifyAdjacencyMatrix(light);
                        break;
                    case GraphRepresentationType.EDGE_LIST:
                        parsed = GraphParser.parseWeightedEdgeListString(this.from);
                        light = GraphRepresentationConverter.removeWeightFromEdgeList(parsed);
                        this.from = GraphParser.stringifyEdgeList(light);
                        break;
                }
            }
            else {
                let parsed;
                let light;
                switch (this.fromType) {
                    case GraphRepresentationType.ADJACENCY_LIST:
                        parsed = GraphParser.parseAdjacencyListString(this.from);
                        light = GraphRepresentationConverter.addWeightToAdjacencyList(parsed);
                        this.from = GraphParser.stringifyWeightedAdjacencyList(light);
                        break;
                    case GraphRepresentationType.ADJACENCY_MATRIX:
                        parsed = GraphParser.parseAdjacencyMatrixString(this.from);
                        this.from = GraphParser.stringifyAdjacencyMatrix(parsed);
                        break;
                    case GraphRepresentationType.EDGE_LIST:
                        parsed = GraphParser.parseEdgeListString(this.from);
                        light = GraphRepresentationConverter.addWeightToEdgeList(parsed);
                        this.from = GraphParser.stringifyEdgeList(light);
                        break;
                }
            }
            this.isWeighted = !this.isWeighted;
        }
        catch (e) {
            this.onError("Failed to parse the inputted graph");
            return;
        }
    }
    addRemoveDirection(toDirected) {
        const execute = (adjmat) => {
            if (toDirected)
                return GraphRepresentationConverter.toDirectedGraph(adjmat);
            else
                return GraphRepresentationConverter.toUndirectedGraph(adjmat);
        };
        try {
            if (this.isWeighted) {
                let parsed;
                let directmat;
                switch (this.fromType) {
                    case GraphRepresentationType.ADJACENCY_LIST:
                        parsed = GraphRepresentationConverter.toWeightedAdjacencyMatrix(GraphParser.parseWeightedAdjacencyListString(this.from));
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyWeightedAdjacencyList(GraphRepresentationConverter.fromWeightedAdjacencyMatrix(directmat));
                        break;
                    case GraphRepresentationType.ADJACENCY_MATRIX:
                        parsed = GraphParser.parseAdjacencyMatrixString(this.from);
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyAdjacencyMatrix(directmat);
                        break;
                    case GraphRepresentationType.EDGE_LIST:
                        parsed = GraphRepresentationConverter.toWeightedAdjacencyMatrix(GraphRepresentationConverter.fromWeightedEdgeList(GraphParser.parseWeightedEdgeListString(this.from)));
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyEdgeList(GraphRepresentationConverter.toWeightedEdgeList(GraphRepresentationConverter.fromWeightedAdjacencyMatrix(directmat)));
                        break;
                }
            }
            else {
                let parsed;
                let directmat;
                switch (this.fromType) {
                    case GraphRepresentationType.ADJACENCY_LIST:
                        parsed = GraphRepresentationConverter.toAdjacencyMatrix(GraphParser.parseAdjacencyListString(this.from));
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyAdjacencyList(GraphRepresentationConverter.fromAdjacencyMatrix(directmat));
                        break;
                    case GraphRepresentationType.ADJACENCY_MATRIX:
                        parsed = GraphParser.parseAdjacencyMatrixString(this.from);
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyAdjacencyMatrix(directmat);
                        break;
                    case GraphRepresentationType.EDGE_LIST:
                        parsed = GraphRepresentationConverter.toAdjacencyMatrix(GraphRepresentationConverter.fromEdgeList(GraphParser.parseEdgeListString(this.from)));
                        directmat = execute(parsed);
                        this.from = GraphParser.stringifyEdgeList(GraphRepresentationConverter.toEdgeList(GraphRepresentationConverter.fromAdjacencyMatrix(directmat)));
                        break;
                }
            }
        }
        catch (e) {
            console.warn(e);
            this.onError("Failed to parse the inputted graph");
            return;
        }
    }
    static toAdjacencyMatrix(adjlist) {
        const matrix = [];
        const adjarr = Object.entries(adjlist);
        matrix.push(adjarr.map(x => x[0]));
        for (let r = 0; r < adjarr.length; r++) {
            matrix.push(Array(adjarr.length).fill(0));
            for (let c = 0; c < adjarr.length; c++) {
                if (!adjarr[r][1].includes(adjarr[c][0]))
                    continue;
                matrix[r + 1][c] = 1;
            }
        }
        return matrix;
    }
    static toWeightedAdjacencyMatrix(adjlist) {
        const matrix = [];
        const adjarr = Object.entries(adjlist);
        matrix.push(adjarr.map(x => x[0]));
        for (let r = 0; r < adjarr.length; r++) {
            matrix.push(Array(adjarr.length).fill(0));
            for (let c = 0; c < adjarr.length; c++) {
                const findme = adjarr[r][1].find(x => x[0] === adjarr[c][0]);
                if (!findme)
                    continue;
                matrix[r + 1][c] = findme[1];
            }
        }
        return matrix;
    }
    static toEdgeList(adjlist) {
        const edgeList = [];
        for (let src in adjlist) {
            for (let endpoint of adjlist[src]) {
                edgeList.push([src, endpoint]);
            }
        }
        return edgeList;
    }
    static toWeightedEdgeList(adjlist) {
        const edgeList = [];
        for (let src in adjlist) {
            for (let endpoint of adjlist[src]) {
                edgeList.push([src, endpoint[0], endpoint[1]]);
            }
        }
        return edgeList;
    }
    static fromAdjacencyMatrix(adjmat) {
        const adjlist = {};
        const vertices = adjmat.shift();
        for (let vertice of vertices) {
            adjlist[vertice] = [];
        }
        for (let r = 0; r < adjmat.length; r++) {
            for (let c = 0; c < adjmat.length; c++) {
                if (adjmat[r][c] != 0) {
                    adjlist[vertices[r]].push(vertices[c].toString());
                }
            }
        }
        return adjlist;
    }
    static fromWeightedAdjacencyMatrix(adjmat) {
        const adjlist = {};
        const vertices = adjmat.shift();
        for (let vertice of vertices) {
            adjlist[vertice] = [];
        }
        for (let r = 0; r < adjmat.length; r++) {
            for (let c = 0; c < adjmat.length; c++) {
                if (adjmat[r][c] != 0) {
                    adjlist[vertices[r]].push([vertices[c].toString(), adjmat[r][c]]);
                }
            }
        }
        return adjlist;
    }
    static fromEdgeList(edgelist) {
        const adjlist = {};
        for (let edge of edgelist) {
            if (!(edge[0] in adjlist)) {
                adjlist[edge[0]] = [edge[1]];
            }
            else {
                adjlist[edge[0]].push(edge[1]);
            }
        }
        return adjlist;
    }
    static fromWeightedEdgeList(edgelist) {
        const adjlist = {};
        for (let edge of edgelist) {
            if (!(edge[0] in adjlist)) {
                adjlist[edge[0]] = [[edge[1], edge[2]]];
            }
            else {
                adjlist[edge[0]].push([edge[1], edge[2]]);
            }
        }
        return adjlist;
    }
    static addWeightToAdjacencyList(adjlist) {
        const weighted = {};
        for (const vertex in adjlist) {
            weighted[vertex] = [];
            for (const edge of adjlist[vertex]) {
                weighted[vertex].push([edge, 1]);
            }
        }
        return weighted;
    }
    static addWeightToEdgeList(edgelist) {
        const weighted = [];
        for (const edge of edgelist) {
            weighted.push([edge[0], edge[1], 1]);
        }
        return weighted;
    }
    static removeWeightFromAdjacencyList(adjlist) {
        const light = {};
        for (const vertex in adjlist) {
            light[vertex] = [];
            for (const edge of adjlist[vertex]) {
                light[vertex].push(edge[0]);
            }
        }
        return light;
    }
    static removeWeightFromAdjacencyMatrix(adjmat) {
        const light = [adjmat.shift()];
        for (let r = 0; r < adjmat.length; r++) {
            light.push([]);
            for (let c = 0; c < adjmat.length; c++) {
                light[r + 1].push((adjmat[r][c] === 0 ? 0 : 1));
            }
        }
        return light;
    }
    static removeWeightFromEdgeList(edgelist) {
        const light = [];
        for (const edge of edgelist) {
            light.push([edge[0], edge[1]]);
        }
        return light;
    }
    static toUndirectedGraph(adjmat) {
        const header = adjmat.shift();
        const result = adjmat.map(r => r.map(c => c));
        for (let r = 0; r < adjmat.length; r++) {
            for (let c = 0; c < r; c++) {
                if (result[r][c] > 0 && result[c][r] > 0) {
                    result[r][c] = 0;
                }
            }
        }
        result.unshift(header);
        return result;
    }
    static toDirectedGraph(adjmat) {
        const header = adjmat.shift();
        const result = adjmat.map(r => r.map(c => c));
        for (let r = 0; r < adjmat.length; r++) {
            for (let c = 0; c < r; c++) {
                if (result[r][c] > 0) {
                    result[c][r] = result[r][c];
                }
                else if (result[c][r] > 0) {
                    result[r][c] = result[c][r];
                }
            }
        }
        result.unshift(header);
        return result;
    }
}
GraphRepresentationConverter.ALL_TYPES = [
    ["Adjacency List", GraphRepresentationType.ADJACENCY_LIST],
    ["Adjacency Matrix", GraphRepresentationType.ADJACENCY_MATRIX],
    ["Edge List", GraphRepresentationType.EDGE_LIST]
];
